import Swiper from 'swiper';
import 'swiper/css';
import 'ScssComponents/team-slider.scss';
import 'ScssComponents/team-card.scss';

import { exist } from '../utils';

export const teamSlider = () => {
	const SELECTORS = {
		wrapper: '.js-team-slider-wrapper',
		slider: '.js-team-slider',
	};
	const $sliders = document.querySelectorAll(SELECTORS.wrapper);

	if (!exist($sliders)) return;

	$sliders.forEach(($wrapper) => {
		const $slider = $wrapper.querySelector(SELECTORS.slider);

		if (!exist($slider)) return;

		const slider = new Swiper($slider, {
			watchOverflow: true,
			autoHeight: true,
			speed: 800,
			slidesPerView: 'auto',
		});
	});
};
